import { Button, Snackbar, Alert } from "@mui/material";
import Polls from "./Polls";
import Reports from "./Reports";
import { useState } from 'react';
import Antonela from "./Antonela";
import Warren from "./Warren";

export const Menu = ({ permissions }) => {
    const [showReports, setShowReports] = useState(false);
    const [showPolls, setShowPolls] = useState(false);
    const [showWarren, setShowWarren] = useState(false);
    const [showAntonela, setShowAntonela] = useState(false);

    const [openPermissios, setOpenPermissions] = useState(false);
    const [openReport, setOpenReport] = useState(false);
    const [success, setSuccess] = useState(false);

    const handleReports = async () => {
        if (permissions === "report:read") {
            await getReports();
        } else {
            setOpenPermissions(true);
        }
    }

    const getReports = async () => {
        try {
            const res = await fetch("https://analytics-backend-nine.vercel.app/", {
                method: "GET",
                headers: { 'Content-Type': 'application/json' }
            })
            if (res.ok) {
                setSuccess(true);
            }
            setOpenReport(true);
            setTimeout(() => setShowReports(true), 1500);
        } catch (error) {
            console.log("ERROR", error)
        }
    }


    if (showPolls) {
        return <Polls />
    } else if (showReports) {
        return <Reports />
    } else if (showAntonela) {
        return <Antonela />
    } else if (showWarren) {
        return <Warren />
    } else {
        return <>
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                gap: '30px',
                marginTop: '50px',
                width: '100%',
                alignItems: 'center',
                justifyContent: 'center'
            }}>
                <Button
                    variant="contained"
                    style={{
                        backgroundColor: "#d23342",
                        padding: "20px 40px",
                        fontSize: "18px",
                    }}
                    onClick={async () => await handleReports()}>
                    Dashboard Nasser Al-Khelaïfi
                    </Button>
                <Button
                    variant="contained"
                    style={{
                        backgroundColor: "#d23342",
                        padding: "20px 40px",
                        fontSize: "18px",
                    }}
                    onClick={() => setShowPolls(true)}>
                    Dashboard Thomas
                </Button>
                <Button
                    variant="contained"
                    style={{
                        backgroundColor: "#d23342",
                        padding: "20px 40px",
                        fontSize: "18px",
                    }}
                    onClick={() => setShowWarren(true)}>
                    Dashboard Warren
                </Button>
                <Button
                    variant="contained"
                    style={{
                        backgroundColor: "#d23342",
                        padding: "20px 40px",
                        fontSize: "18px",
                    }}
                    onClick={() => setShowAntonela(true)}>
                    Dashboard Antonela y Anti Messi
                </Button>
            </div>
            <Snackbar open={openPermissios} autoHideDuration={6000} onClose={() => setOpenPermissions(!openPermissios)}>
                <Alert onClose={() => setOpenPermissions(!openPermissios)} severity={"error"} sx={{ width: '100%' }}>
                    No tenés permisos para ver el reporte
                </Alert>
            </Snackbar>
            <Snackbar open={openReport} autoHideDuration={6000} onClose={() => setOpenReport(!openReport)}>
                <Alert onClose={() => setOpenReport(!openReport)} severity={success ? "success" : "error"} sx={{ width: '100%' }}>
                    {success ? "Reporte generado!" : "Error el generar reporte"}
                </Alert>
            </Snackbar>
        </>
    }
}