import psgBanner from '../images/psg2.png';
import { Button } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import LoginContext from '../components/LoginContext';
import { useContext } from 'react';

const Header = () => {
    const { loggedIn, setLoggedIn, setBack } = useContext(LoginContext);

    return (<div style={{
        width: "100%",
        height: "100px",
        padding: "0.5%",
        backgroundColor: "#1a2b4c",
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    }}>
        {
            loggedIn ? <Button
                variant="text"
                onClick={() => null}
                startIcon={<ArrowBackIcon />}
                style={{
                    color: "transparent",
                    marginRight: "auto",
                    padding: "20px 40px",
                    fontSize: "18px",
                }}
            >Volver</Button> : null
        }
        <img src={psgBanner} height={100} />
        {
            loggedIn ?
                <Button
                    variant="text"
                    style={{
                        padding: "20px 40px",
                        fontSize: "18px",
                        color: "white",
                        marginLeft: "auto"
                    }}
                    onClick={() => setLoggedIn(false)}
                >Cerrar sesión</Button> : null
        }

    </div>)
}

export default Header;